export default {
    name: "app-goods-full-reduce",

    props: {
        theme: Object,
        full_reduce: Object,
        sign: String
    },

    data() {
        return {
            textWidth: 0,
            resultWidth: 0,
            list: [],
            opacity: false
        }
    },
    methods: {
        route() {
            uni.navigateTo({
                url: '/pages/full_reduce/index/index'
            })
        },
        initSize: function () {
            let query = [];
            for (let i = 0; i < this.list.length; i++) {
                query.push(
                    new Promise(resolve => {
                        let query = uni.createSelectorQuery().in(this);
                        query.select(`#bd-${i}-text`).boundingClientRect().exec(ret => {
                            if (ret[0]) {
                                this.textWidth += (ret[0].width + uni.upx2px(10));
                            }
                            resolve();
                        });
                    })
                )
            }
            Promise.all(query).then(() => {
                this.resultWidth = this.textWidth;
            });
        }
    },
    watch: {
        'full_reduce.rule' : {
            immediate: true,
            handler(newVal) {
                this.list = newVal;
                this.textWidth = 0;
                setTimeout(() => {
                    this.initSize();
                }, 500);
            },
            deep: true
        },
        resultWidth: {
            handler(newVal) {
                if (newVal > uni.upx2px(552)) {
                    this.list = this.list.slice(0, this.list.length - 1);
                    this.textWidth = 0;
                    this.$nextTick(() => {
                        this.initSize();
                    });
                } else {
                    this.opacity = true;
                }
            }
        }
    },
    mounted() {
        setTimeout(() => {
            this.list = this.full_reduce.rule;
            this.textWidth = 0;
            setTimeout(() => {
                this.initSize();
            }, 500);
        }, 500);
    }

}
